import React from "react";
import {
  Typography,
  Box,
  Divider,
  Chip,
  Alert,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Stepper,
  Step,
  StepLabel,
  StepContent,
} from "@mui/material";
import AccessibleIcon from "@mui/icons-material/Accessible";

const ExemploMatriz = ({ titulo, linhas }) => (
  <Box sx={{ my: 2 }}>
    <Typography
      variant="caption"
      sx={{ fontWeight: 700, mb: 1, display: "block" }}
    >
      {titulo}
    </Typography>
    <TableContainer component={Paper} variant="outlined" sx={{ maxWidth: 500 }}>
      <Table size="small">
        <TableHead>
          <TableRow sx={{ backgroundColor: "#f5f5f5" }}>
            <TableCell sx={{ fontWeight: 700, fontSize: "0.75rem" }}>
              Departamento
            </TableCell>
            <TableCell sx={{ fontWeight: 700, fontSize: "0.75rem" }}>
              Prdio
            </TableCell>
            <TableCell sx={{ fontWeight: 700, fontSize: "0.75rem" }}>
              Distncia
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {linhas.map((l, i) => (
            <TableRow
              key={i}
              sx={l.destaque ? { backgroundColor: "#e8f5e9" } : {}}
            >
              <TableCell sx={{ fontSize: "0.75rem" }}>{l.dept}</TableCell>
              <TableCell sx={{ fontSize: "0.75rem" }}>{l.predio}</TableCell>
              <TableCell
                sx={{ fontSize: "0.75rem", fontWeight: l.destaque ? 700 : 400 }}
              >
                {l.dist}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  </Box>
);

const AjudaDistancias = () => {
  return (
    <Box sx={{ p: 2, maxWidth: 750 }}>
      <Typography variant="h5" gutterBottom>
        Distncias  Ajuda e Tutorial
      </Typography>
      <Divider sx={{ mb: 2 }} />

      {/* ============ SEO 1: CONCEITO GERAL ============ */}
      <Typography variant="h6" gutterBottom>
        O que so as distncias?
      </Typography>
      <Typography variant="body2" paragraph>
        A matriz de distncias define a{" "}
        <b>distncia entre cada departamento e cada prdio</b> do campus. O
        solver usa esses valores para decidir em qual prdio alocar cada turma 
        quanto <b>menor</b> a distncia, maior a prioridade.
      </Typography>
      <Typography variant="body2" paragraph>
        <b>Regra prtica:</b> distncia <b>0</b> = prioridade mxima. Distncia{" "}
        <b>999</b> = penalidade (o solver evita esse prdio).
      </Typography>

      <Alert severity="info" sx={{ mb: 2, fontSize: "0.8rem" }}>
        <b>Todas as combinaes precisam existir!</b> O solver exige que cada
        departamento tenha uma distncia cadastrada para cada prdio. Se faltar
        alguma, o sistema mostrar um alerta vermelho no topo desta pgina  use
        o boto "Exibir" para ver quais esto faltando.
      </Alert>

      <Divider sx={{ my: 2 }} />

      {/* ============ SEO 2: ACESSIBILIDADE ============ */}
      <Typography
        variant="h6"
        gutterBottom
        sx={{ display: "flex", alignItems: "center", gap: 1 }}
      >
        <AccessibleIcon /> Distncias para Acessibilidade
      </Typography>

      <Typography variant="body2" paragraph>
        Quando uma turma tem uma solicitao de acessibilidade (ex: Trreo), o
        departamento dela  trocado por um <b>departamento virtual</b> (ex: DC {" "}
        <b>TERREO-DC</b>). Esse departamento virtual precisa ter suas distncias
        configuradas <b>nesta pgina</b>.
      </Typography>

      <Alert severity="success" sx={{ mb: 2, fontSize: "0.8rem" }}>
        <b>A lgica  simples:</b> configure distncia <b>0</b> para prdios que
        atendem ao requisito e <b>999</b> para os que no atendem. O solver far
        o resto!
      </Alert>

      {/* ---- Exemplo visual ---- */}
      <Typography variant="subtitle1" sx={{ fontWeight: 700, mb: 1 }}>
        Exemplo: Turma do DC com aluno cadeirante (Trreo)
      </Typography>

      <Typography variant="body2" sx={{ fontWeight: 600, mb: 1 }}>
        Antes (sem acessibilidade):
      </Typography>
      <Typography variant="body2" paragraph>
        A turma pertence ao DC. O solver v que AT02 e AT02(T) tm a mesma
        distncia (50m). Ele pode alocar em qualquer um  inclusive no 2 andar.
      </Typography>
      <ExemploMatriz
        titulo="Distncias do DC (todas iguais para prdios prximos):"
        linhas={[
          { dept: "DC", predio: "AT02", dist: "50" },
          { dept: "DC", predio: "AT02(T)", dist: "50" },
          { dept: "DC", predio: "AT03", dist: "300" },
        ]}
      />

      <Typography variant="body2" sx={{ fontWeight: 600, mb: 1 }}>
        Depois (com acessibilidade aplicada):
      </Typography>
      <Typography variant="body2" paragraph>
        A turma agora pertence a <b>TERREO-DC</b>. Voc configura distncia{" "}
        <b>0</b> para prdios com trreo e <b>999</b> para os demais:
      </Typography>
      <ExemploMatriz
        titulo="O que voc configura para TERREO-DC:"
        linhas={[
          { dept: "TERREO-DC", predio: "AT02", dist: "999" },
          { dept: "TERREO-DC", predio: "AT02(T)", dist: "0", destaque: true },
          { dept: "TERREO-DC", predio: "AT03", dist: "999" },
          { dept: "TERREO-DC", predio: "AT03(T)", dist: "0", destaque: true },
        ]}
      />
      <Typography variant="body2" paragraph>
        Resultado: o solver v que AT02(T) tem distncia <b>0</b>  prioridade
        mxima. AT02 e AT03 ficam com 999  o solver os evita. A turma vai para
        o trreo!
      </Typography>

      <Divider sx={{ my: 2 }} />

      {/* ============ SEO 3: PASSO A PASSO ============ */}
      <Typography variant="h6" gutterBottom>
        Passo a passo: configurar acessibilidade
      </Typography>

      <Alert
        severity="info"
        variant="outlined"
        sx={{ mb: 2, fontSize: "0.8rem" }}
      >
        Estes passos devem ser seguidos <b>na ordem</b>. O passo 4 (configurar
        distncias) s funciona depois do passo 3 (aplicar solicitaes).
      </Alert>

      <Stepper orientation="vertical" sx={{ mb: 2 }}>
        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              1. Criar prdios particionados (Prdios e Salas)
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Crie verses separadas dos prdios para as salas especiais.
              Exemplo: <b>AT02(T)</b> s com salas do trreo, <b>AT05.Pr</b>
              s com salas com prancheta. O prdio original fica com as demais
              salas.
            </Typography>
            <Alert
              severity="warning"
              variant="outlined"
              sx={{ fontSize: "0.75rem" }}
            >
              Sem esse passo, o solver no consegue diferenciar salas do trreo
              de salas em andares superiores  elas estariam todas no mesmo
              prdio.
            </Alert>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              2. Marcar turmas com solicitao (Turmas  boto direito)
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Na pgina <b>Turmas</b>, clique com o{" "}
              <b>boto direito do mouse</b> na turma  escolha o tipo de
              solicitao. A turma ser marcada com um chip colorido:
            </Typography>
            <Box sx={{ display: "flex", flexWrap: "wrap", gap: 0.5, mb: 1 }}>
              <Chip
                label="Trreo"
                size="small"
                sx={{ backgroundColor: "#4caf50", color: "#fff" }}
              />
              <Chip
                label="Prancheta"
                size="small"
                sx={{ backgroundColor: "#ff9800", color: "#fff" }}
              />
              <Chip
                label="QV"
                size="small"
                sx={{ backgroundColor: "#2e7d32", color: "#fff" }}
              />
              <Chip
                label="QB"
                size="small"
                sx={{ backgroundColor: "#1565c0", color: "#fff" }}
              />
              <Chip
                label="Lab"
                size="small"
                sx={{ backgroundColor: "#7b1fa2", color: "#fff" }}
              />
              <Chip
                label="Esp-Norte"
                size="small"
                sx={{ backgroundColor: "#c62828", color: "#fff" }}
              />
              <Chip
                label="Esp-Sul"
                size="small"
                sx={{ backgroundColor: "#00838f", color: "#fff" }}
              />
            </Box>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              3. Aplicar solicitaes (pgina Solicitaes)
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Na pgina <b>Solicitaes</b>, clique em <b>"Aplicar Todas"</b>. O
              departamento das turmas ser trocado para o departamento virtual
              (ex: DC  TERREO-DC).
            </Typography>
            <Alert
              severity="info"
              variant="outlined"
              sx={{ fontSize: "0.75rem" }}
            >
              S aps aplicar  que os departamentos virtuais existem e aparecem
              nesta pgina de Distncias.
            </Alert>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              4. Configurar distncias dos departamentos virtuais (esta pgina)
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Os departamentos virtuais (TERREO-DC, PRANCHETA-DC, etc.) agora
              aparecem na lista. Para cada um:
            </Typography>
            <Box component="ul" sx={{ pl: 2, "& li": { fontSize: "0.8rem" } }}>
              <li>
                <b>0</b>  prdios que atendem ao requisito
              </li>
              <li>
                <b>999</b>  prdios que NO atendem
              </li>
            </Box>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              5. Rodar o Solver
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Rode o solver normalmente. As turmas com solicitao sero
              alocadas nos prdios com distncia 0 (os adequados).
            </Typography>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              6. (Opcional) Reverter solicitaes
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Na pgina <b>Solicitaes</b>, clique <b>"Reverter Todas"</b>
              para restaurar os departamentos originais.
            </Typography>
          </StepContent>
        </Step>
      </Stepper>

      <Divider sx={{ my: 2 }} />

      {/* ============ SEO 4: TABELA DE REFERNCIA ============ */}
      <Typography variant="h6" gutterBottom>
        Referncia rpida: tipos de solicitao
      </Typography>
      <Typography variant="body2" paragraph>
        O departamento virtual  gerado como <b>PREFIXO-DEPARTAMENTO</b> (ex:
        TERREO-DC, LAB-DFCM):
      </Typography>

      <TableContainer component={Paper} variant="outlined" sx={{ mb: 2 }}>
        <Table size="small">
          <TableHead>
            <TableRow sx={{ backgroundColor: "#f5f5f5" }}>
              <TableCell sx={{ fontWeight: 700, fontSize: "0.75rem" }}>
                Tipo
              </TableCell>
              <TableCell sx={{ fontWeight: 700, fontSize: "0.75rem" }}>
                Sufixo no prdio
              </TableCell>
              <TableCell sx={{ fontWeight: 700, fontSize: "0.75rem" }}>
                Depto virtual (ex: DC)
              </TableCell>
              <TableCell sx={{ fontWeight: 700, fontSize: "0.75rem" }}>
                Como configurar
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {[
              {
                tipo: "Trreo",
                sufixo: "(T)",
                dept: "TERREO-DC",
                dica: "0 para prdios com (T), 999 demais",
              },
              {
                tipo: "Prancheta",
                sufixo: ".Pr",
                dept: "PRANCHETA-DC",
                dica: "0 para prdios com .Pr, 999 demais",
              },
              {
                tipo: "Quadro Verde",
                sufixo: "(QV)",
                dept: "QV-DC",
                dica: "0 para prdios com (QV), 999 demais",
              },
              {
                tipo: "Quadro Branco",
                sufixo: "(QB)",
                dept: "QB-DC",
                dica: "0 para prdios com (QB), 999 demais",
              },
              {
                tipo: "Laboratrio",
                sufixo: "(LAB)",
                dept: "LAB-DC",
                dica: "0 para prdios com (LAB), 999 demais",
              },
              {
                tipo: "Esp-Norte",
                sufixo: "(N)",
                dept: "NORTE-DC",
                dica: "0 para prdios com (N), 999 demais",
              },
              {
                tipo: "Esp-Sul",
                sufixo: "(S)",
                dept: "SUL-DC",
                dica: "0 para prdios com (S), 999 demais",
              },
            ].map((row, i) => (
              <TableRow key={i}>
                <TableCell sx={{ fontSize: "0.75rem" }}>{row.tipo}</TableCell>
                <TableCell sx={{ fontSize: "0.75rem" }}>
                  <code>{row.sufixo}</code>
                </TableCell>
                <TableCell sx={{ fontSize: "0.75rem", fontWeight: 600 }}>
                  {row.dept}
                </TableCell>
                <TableCell sx={{ fontSize: "0.75rem" }}>{row.dica}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Divider sx={{ my: 2 }} />

      {/* ============ SEO 5: DICAS ============ */}
      <Typography variant="h6" gutterBottom>
        Dicas importantes
      </Typography>
      <Alert severity="warning" sx={{ mb: 1.5, fontSize: "0.8rem" }}>
        <b>Ordem dos passos:</b> Primeiro aplique as solicitaes na pgina
        Solicitaes, depois configure distncias aqui. Se configurar antes de
        aplicar, os departamentos virtuais ainda no existiro.
      </Alert>
      <Alert severity="info" sx={{ mb: 1.5, fontSize: "0.8rem" }}>
        <b>Prdios particionados:</b> AT02 (andares superiores) e AT02(T)
        (trreo) devem ser prdios <b>distintos</b> com salas distintas. Se
        ficarem juntos, o solver no consegue diferenciar.
      </Alert>
      <Alert severity="info" sx={{ mb: 1.5, fontSize: "0.8rem" }}>
        <b>Conveno de nomes nos prdios:</b> Use <code>(T)</code> para trreo,{" "}
        <code>.Pr</code> para prancheta (ex: AT05.Pr), <code>(LAB)</code> para
        laboratrio, etc. Isso facilita identificar quais prdios recebem dist.
        0 vs 999.
      </Alert>
      <Alert severity="info" sx={{ fontSize: "0.8rem" }}>
        <b>Mltiplos departamentos:</b> Se turmas do DC e do DFCM tm
        solicitao de Trreo, sero criados TERREO-DC e TERREO-DFCM. Configure
        as distncias de cada um separadamente (ambos com 0 para prdios com
        trreo e 999 para os demais).
      </Alert>
    </Box>
  );
};

export default AjudaDistancias;
