import React from "react";
import DistanciaForm from "../../forms/distanciaForm.component";
import PageHeader from "../../re-usable/page-header.component";
import DirectionsWalkIcon from "@mui/icons-material/DirectionsWalk";
import {
  Modal,
  TableBody,
  TableCell,
  TableRow,
  Grid,
  Toolbar,
  TextField,
  DialogContent,
  Dialog,
  Button,
  IconButton,
} from "@mui/material";
import HelpIcon from "@mui/icons-material/Help";
import useTable from "../../re-usable/useTable";
import DistanciasDataService from "../../../services/distancias";
import TurmasDataService from "../../../services/turmas";
import SalasDataService from "../../../services/salas";
import { useEffect, useState } from "react";
import { TableContainer, Paper } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import SearchIcon from "@mui/icons-material/Search";
import InputAdornment from "@mui/material/InputAdornment";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import Mensagem from "../../re-usable/mensagem.component";
import ConfirmDialog from "../../re-usable/confirmDialog.component";
import handleServerResponses from "../../../services/response-handler";
import { Checkbox } from "@mui/material";
import FileFormDistancias from "../../forms/fileFormDistancia.component";
import { Alert } from "@mui/material";
import AjudaDistancias from "../help/ajuda-distancias.component";

const tableRowCss = {
  "& .MuiTableCell-root": {
    padding: 1,
  },
};

const tableStyle = {
  "& thead th span": {
    fontWeight: "600",
    fontSize: "0.7rem",
  },
  "& tbody td": {
    fontSize: "0.7rem",
  },
};

const headCells = [
  { id: "actions", label: "Aes", disableSorting: true },
  { id: "predio", label: "Prdio" },
  { id: "departamento", label: "Departamento" },
  { id: "distancia", label: "Distncia" },
  { id: "status", label: "Status" },
];

// Funo para obter valores nicos de um array
const arrayUnique = (array) => [...new Set(array)];

// Funo para limpar aspas, espaos e normalizar para minsculas
const normalizarDept = (dept) => {
  if (!dept) return ""; // Retorna string vazia se a entrada for nula/indefinida
  return String(dept)
    .trim()
    .replace(/^['"]|['"]$/g, "")
    .toLowerCase();
};

const DistanciasMatriz = (props) => {
  const { user, logout, config } = props;

  const [distancias, setDistancias] = useState([]);
  const [predios, setPredios] = useState([]);
  const [departamentosTurmas, setDepartamentosTurmas] = useState([]); // Armazena depts J NORMALIZADOS das turmas
  const [departamentos, setDepartamentos] = useState([]); // Lista final, unida, normalizada e nica de todos os depts
  const [temTodos, setTemTodos] = useState(true);
  const [indiceDistancias, setIndiceDistancias] = useState({});
  const [distanciaTableObjs, setDistanciaTableObjs] = useState([]);
  const [openModalForm, setOpenModalForm] = React.useState(false);
  const [openModalFile, setOpenModalFile] = React.useState(false);
  const [distanciaEdit, setDistanciaEdit] = useState(null);
  const [updatingD, setUpdatingD] = useState(false);
  const [filterFn, setFilterFn] = useState({
    fn: (items) => {
      return items;
    },
  });
  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });
  const [confirmDialog, setConfirmDialog] = useState({
    isOpen: false,
    title: "",
    subtitle: "",
  });
  const [selected, setSelected] = React.useState([]);
  const [openHelp, setOpenHelp] = useState(false);

  const handleCloseModalForm = () => {
    setOpenModalForm(false);
    setSelected([]);
  };
  const handleOpenModalFile = () => setOpenModalFile(true);
  const handleCloseModalFile = () => setOpenModalFile(false);

  useEffect(() => {
    if (user) {
      retornaPredios();
      retornaDistancias();
      retornaDepartamentosTurmas();
    }
  }, [notify, user]);

  // --- USEEFFECT PARA UNIO: Roda quando depts de turmas OU distncias mudam ---
  useEffect(() => {
    // Normaliza os departamentos vindos das distncias
    const deptsDistanciasNormalizados = arrayUnique(
      distancias.map((d) => normalizarDept(d.departamento)),
    );

    // A lista 'departamentosTurmas' j est normalizada. Agora unimos as duas listas limpas.
    const todosDepts = arrayUnique([
      ...departamentosTurmas,
      ...deptsDistanciasNormalizados,
    ]);

    setDepartamentos(todosDepts.sort()); // Ordena para consistncia na exibio
  }, [departamentosTurmas, distancias]);

  useEffect(() => {
    if (distancias.length > 0) {
      retornaIndiceDistancias();
    }
    // A verificao de 'temTodos' deve ser feita aps a tabela ser montada
  }, [distancias]);

  useEffect(() => {
    if (
      Object.keys(indiceDistancias).length > 0 &&
      predios.length > 0 &&
      departamentos.length > 0
    ) {
      retornaDistanciasTableObjs();
    }
  }, [indiceDistancias, predios, departamentos]);

  // Verifica se todas as distncias esto preenchidas aps a tabela ser gerada
  useEffect(() => {
    if (distanciaTableObjs.length > 0) {
      const estaoTodosPreenchidos = distanciaTableObjs.every(
        (item) => item.status === "OK",
      );
      setTemTodos(estaoTodosPreenchidos);
    }
  }, [distanciaTableObjs]);

  // --- FUNES DE BUSCA CORRIGIDAS ---

  // Busca depts das turmas e J NORMALIZA
  const retornaDepartamentosTurmas = () => {
    TurmasDataService.getDepartamentos()
      .then((response) => {
        const depts = response.data || [];
        // CORREO: Normaliza os dados assim que eles chegam do backend
        const deptsNormalizados = depts.map((d) => normalizarDept(d));
        setDepartamentosTurmas(arrayUnique(deptsNormalizados));
      })
      .catch((err) => {
        console.error("Erro ao buscar depts das turmas:", err);
        setDepartamentosTurmas([]);
        const errorResponse = err.response || {
          data: { msg: "Erro ao buscar depts das turmas." },
        };
        handleServerResponses("turmas", errorResponse, setNotify);
      });
  };

  const retornaDistancias = () => {
    DistanciasDataService.getAll()
      .then((response) => {
        setDistancias(response.data || []);
      })
      .catch((err) => {
        console.error("Erro ao buscar distncias:", err);
        const errorResponse = err.response || {
          data: { msg: "Erro de conexo ao buscar distncias." },
        };
        handleServerResponses("distancias", errorResponse, setNotify);
      });
  };

  const retornaPredios = () => {
    SalasDataService.getPredios()
      .then((response) => {
        setPredios(response.data || []);
      })
      .catch((err) => {
        if (err.response?.data?.notAuth) {
          logout();
        } else {
          const errorResponse = err.response || {
            data: { msg: "Erro de conexo ao buscar prdios." },
          };
          handleServerResponses("salas", errorResponse, setNotify);
        }
      });
  };

  const retornaIndiceDistancias = () => {
    const indexDist = {};
    distancias.forEach((cur) => {
      const predioNormalizado = cur.predio.trim(); // Limpa espaos do prdio tambm
      const deptNormalizado = normalizarDept(cur.departamento); // Normaliza o depto para usar como chave
      if (!indexDist[predioNormalizado]) indexDist[predioNormalizado] = {};
      indexDist[predioNormalizado][deptNormalizado] = {
        distancia: cur.valorDist,
        _id: cur._id,
      };
    });
    setIndiceDistancias(indexDist);
  };

  const retornaDistanciasTableObjs = () => {
    let distTableObjArray = [];
    predios.forEach((predio) => {
      // A lista 'departamentos' j est limpa, normalizada e nica
      departamentos.forEach((departamentoNormalizado) => {
        const entry =
          indiceDistancias[predio.trim()]?.[departamentoNormalizado];
        const strId = `id_${predio}_${departamentoNormalizado}`;

        distTableObjArray.push({
          _id: entry?._id || strId,
          predio: predio,
          departamento: departamentoNormalizado.toUpperCase(), // Exibe em maisculas para ficar mais legvel
          valorDist: entry?.distancia ?? "-", // Usa ?? para tratar valor 0 corretamente
          status: entry !== undefined ? "OK" : "No Informado",
        });
      });
    });
    setDistanciaTableObjs(distTableObjArray);
  };

  const retornaTemTodos = () => {
    // Esta funo agora  controlada pelo useEffect que verifica a tabela montada
    // A chamada  API pode ser mantida se o backend tiver uma lgica mais complexa
    DistanciasDataService.temTodos()
      .then((res) => {
        // A verificao local  mais confivel neste caso, mas podemos manter a do backend se preferir
        // setTemTodos(res.data.isComplete)
      })
      .catch((err) => console.error("Erro em temTodos:", err));
  };

  const handleSearch = (e) => {
    let target = e.target;
    setFilterFn({
      fn: (items) => {
        if (target.value === "") {
          return items;
        } else {
          return items.filter((distancia) => {
            return (
              distancia.predio
                .toLowerCase()
                .includes(target.value.toLowerCase()) ||
              distancia.departamento
                .toLowerCase()
                .includes(target.value.toLowerCase()) ||
              distancia.status
                .toLowerCase()
                .includes(target.value.toLowerCase())
            );
          });
        }
      },
    });
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = recordsAfterPagingAndSorting().map(
        (distancia) => distancia._id,
      );
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const isSelected = (name) => selected.indexOf(name) !== -1;

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }
    setSelected(newSelected);
  };

  const fileHandleResponse = (res) => {
    setOpenModalFile(false);
    handleServerResponses("distancias", res, setNotify);
    retornaDistancias();
  };

  const addOrEdit = (updating, distancia, resetForm) => {
    let data = { ...distancia };
    const request = updating
      ? DistanciasDataService.updateDistancia(distancia._id, data)
      : DistanciasDataService.addDistancia(data);

    request
      .then((res) => {
        handleServerResponses("distancias", res, setNotify);
        retornaDistancias();
      })
      .catch((err) => {
        const errorResponse = err.response || {
          data: { msg: "Erro de comunicao com o servidor." },
        };
        handleServerResponses("distancias", errorResponse, setNotify);
      });

    setSelected([]);
    resetForm();
    setOpenModalForm(false);
  };

  const onDelete = (distancias) => {
    setConfirmDialog({
      ...confirmDialog,
      isOpen: false,
    });
    // Filtra apenas IDs vlidos do MongoDB (ignora linhas "No Informado" com IDs fabricados)
    const idsValidos = distancias.filter((id) => /^[0-9a-fA-F]{24}$/.test(id));
    if (idsValidos.length === 0) {
      setNotify({
        isOpen: true,
        message:
          "Nenhuma distncia vlida selecionada para excluso. Linhas 'No Informado' no podem ser excludas.",
        type: "warning",
      });
      setSelected([]);
      return;
    }
    let data = { distanciasID: idsValidos };
    DistanciasDataService.deleteDistancias(data)
      .then((res) => {
        handleServerResponses("distancias", res, setNotify);
        retornaDistancias();
      })
      .catch((err) => {
        const errorResponse = err.response || {
          data: {
            msg: "Erro de comunicao com o servidor ao excluir distncias.",
          },
        };
        handleServerResponses("distancias", errorResponse, setNotify);
      });
    setSelected([]);
  };

  const { TblContainer, TblHead, TblPagination, recordsAfterPagingAndSorting } =
    useTable(distanciaTableObjs, headCells, filterFn);

  const openInModalEdit = (distancia) => {
    setUpdatingD(true);
    setDistanciaEdit(distancia);
    setOpenModalForm(true);
  };

  const openInModalNew = () => {
    setUpdatingD(false);
    setDistanciaEdit(null);
    setOpenModalForm(true);
  };

  const forceReload = () => {
    retornaPredios();
    retornaDistancias();
    retornaDepartamentosTurmas();
  };

  const limparDepartamentosFake = () => {
    setConfirmDialog({
      isOpen: true,
      title: "Limpar departamentos obsoletos",
      subtitle:
        "Isso vai corrigir turmas que ainda possuem departamentos fake (ex: DEP-TERREO, TERREO-DEP-TERREO), restaurando o departamento original. Deseja continuar?",
      onConfirm: () => {
        setConfirmDialog({ ...confirmDialog, isOpen: false });
        TurmasDataService.limparDepartamentosFake()
          .then((res) => {
            const data = res.data || {};
            setNotify({
              isOpen: true,
              message:
                data.corrigidas > 0
                  ? `${data.msg} Detalhes: ${data.detalhes?.join("; ") || ""}`
                  : data.msg || "Nenhum departamento fake encontrado.",
              type: data.corrigidas > 0 ? "success" : "info",
            });
            forceReload();
          })
          .catch((err) => {
            const errorResponse = err.response || {
              data: { msg: "Erro ao limpar departamentos fake." },
            };
            handleServerResponses("turmas", errorResponse, setNotify);
          });
      },
    });
  };

  return (
    <>
      <PageHeader
        title="Distncias"
        subtitle="Cadastro, edio e visualizao de distncias"
        icon={<DirectionsWalkIcon />}
      />

      <Mensagem notify={notify} setNotify={setNotify} />
      <ConfirmDialog
        confirmDialog={confirmDialog}
        setConfirmDialog={setConfirmDialog}
      />
      <TableContainer component={Paper}>
        <Modal
          id="modalFile"
          open={openModalFile}
          onClose={handleCloseModalFile}
        >
          <FileFormDistancias
            title={"Adicionar arquivo"}
            closeButton={handleCloseModalFile}
            handleResponse={fileHandleResponse}
          />
        </Modal>
        <Dialog
          maxWidth="md"
          id="modalForm"
          scroll="body"
          open={openModalForm}
          onClose={handleCloseModalForm}
        >
          <DialogContent>
            <DistanciaForm
              addOrEdit={addOrEdit}
              predios={predios}
              departamentos={departamentos}
              tableObjs={distanciaTableObjs}
              updating={updatingD}
              distanciaEdit={distanciaEdit}
              closeModalForm={handleCloseModalForm}
            />
          </DialogContent>
        </Dialog>
        <Toolbar>
          <Grid
            container
            spacing={2}
            sx={{ paddingTop: "12px" }}
            alignItems="center"
            justifyContent="space-between"
            columns={20}
          >
            <Grid
              item
              xs={5}
              sx={{ fontSize: "14px", fontWeight: "500", color: "#666" }}
            >
              Adicionar
            </Grid>
            <Grid
              item
              xs={9}
              sx={{ fontSize: "14px", fontWeight: "500", color: "#666" }}
            >
              Buscar
            </Grid>
            <Grid
              item
              xs={4}
              sx={{ fontSize: "14px", fontWeight: "500", color: "#666" }}
            >
              Mostrar
            </Grid>
            <Grid
              item
              xs={1}
              sx={{ fontSize: "14px", fontWeight: "500", color: "#666" }}
            >
              Ajuda
            </Grid>
            <Grid
              item
              xs={6}
              sx={{ fontSize: "14px", fontWeight: "500", color: "#666" }}
              sm={2}
            >
              <Button
                startIcon={<AddIcon />}
                variant="contained"
                onClick={handleOpenModalFile}
                sx={{
                  fontSize: "12px",
                  paddingTop: "12px",
                  paddingBottom: "12px",
                }}
              >
                Arquivo
              </Button>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Button
                startIcon={<AddIcon />}
                variant="contained"
                onClick={openInModalNew}
                sx={{
                  fontSize: "12px",
                  paddingTop: "12px",
                  paddingBottom: "12px",
                }}
              >
                Formulrio
              </Button>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Button
                variant="outlined"
                onClick={forceReload}
                sx={{
                  fontSize: "12px",
                  paddingTop: "12px",
                  paddingBottom: "12px",
                }}
              >
                Debug: Reload Dados
              </Button>
            </Grid>
            <Grid item xs={6} sm={3}>
              <Button
                variant="outlined"
                color="warning"
                onClick={limparDepartamentosFake}
                sx={{
                  fontSize: "12px",
                  paddingTop: "12px",
                  paddingBottom: "12px",
                }}
              >
                Limpar Depts Obsoletos
              </Button>
            </Grid>
            <Grid item xs={6} sm={6}>
              <TextField
                sx={{ width: "100%" }}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SearchIcon />
                    </InputAdornment>
                  ),
                }}
                onChange={handleSearch}
              />
            </Grid>
            <Grid item xs={6} sm={2}></Grid>
            <Grid item xs={6} sm={2}></Grid>
            <Grid item xs={6} sm={1}>
              <IconButton
                color="inherit"
                edge="start"
                onClick={() => setOpenHelp(true)}
              >
                <HelpIcon />
              </IconButton>
            </Grid>
          </Grid>
        </Toolbar>
        {temTodos ? (
          <></>
        ) : (
          <Alert
            severity="error"
            sx={{ marginTop: "10px" }}
            action={
              <Button
                color="inherit"
                size="small"
                onClick={() => {
                  setFilterFn({
                    fn: (items) =>
                      items.filter((d) => d.status === "No Informado"),
                  });
                }}
              >
                Exibir
              </Button>
            }
          >
            Existem distncias entre prdios e departamentos no informadas. A
            otimizao s podera ser executada com todas as distncias
            cadastradas.
          </Alert>
        )}
        <TblContainer
          sx={tableStyle}
          tableTitle="Lista de distncias"
          numSelected={selected.length}
          deleteSelected={() => {
            setConfirmDialog({
              isOpen: true,
              title: "Deletar Distncias",
              subtitle:
                "Tem certeza que deseja deletar? Voc no pode desfazer esta operao.",
              onConfirm: () => {
                onDelete(selected);
              },
            });
          }}
        >
          <TblHead
            onSelectAllClick={handleSelectAllClick}
            numSelected={selected.length}
            rowCount={recordsAfterPagingAndSorting().length}
          />
          <TableBody>
            {recordsAfterPagingAndSorting().map((distancia, index) => {
              const isItemSelected = isSelected(distancia._id);
              const labelId = `distancias-table-checkbox-${index}`;
              return (
                <TableRow
                  key={distancia._id}
                  sx={tableRowCss}
                  selected={isItemSelected}
                  aria-checked={isItemSelected}
                  role="checkbox"
                  onClick={(event) => handleClick(event, distancia._id)}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isItemSelected}
                      inputProps={{
                        "aria-labelledby": labelId,
                      }}
                    />
                  </TableCell>
                  <TableCell>
                    <IconButton
                      sx={{ padding: "4px" }}
                      color="primary"
                      onClick={(e) => {
                        e.stopPropagation();
                        openInModalEdit(distancia);
                      }} // Adicionado stopPropagation
                    >
                      <EditOutlinedIcon fontSize="small" />
                    </IconButton>
                  </TableCell>
                  <TableCell>{distancia.predio}</TableCell>
                  <TableCell>{distancia.departamento}</TableCell>
                  <TableCell>{distancia.valorDist}</TableCell>
                  <TableCell>{distancia.status}</TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </TblContainer>
        <TblPagination />
      </TableContainer>

      {/* DIALOG DE AJUDA */}
      <Dialog maxWidth="md" open={openHelp} onClose={() => setOpenHelp(false)}>
        <DialogContent>
          <AjudaDistancias />
        </DialogContent>
      </Dialog>
    </>
  );
};

export default DistanciasMatriz;
