import React, {Component, useEffect, useState} from "react";
import PageHeader from '../../re-usable/page-header.component';
import SettingsIcon from '@mui/icons-material/Settings';
import { Box, Paper, Grid, Typography, FormGroup, FormControlLabel, Checkbox, Divider, TextField, Button } from "@mui/material";
import ConfigsDataService from '../../../services/configs';
import Mensagem from '../../re-usable/mensagem.component';
import handleServerResponses from "../../../services/response-handler";

const dias = ['Segunda','Tera','Quarta','Quinta','Sexta','Sbado','Domingo']
const periodos = ['Manh','Tarde','Noite']

const emptyHorarios = {
    "Manh":{
            "Incio":{
                slot1:'',
                slot2:'',
            },
            "Fim":{
                slot1:'',
                slot2:'',
            }
    },
    "Tarde":{
            "Incio":{
                slot1:'',
                slot2:'',
            },
            "Fim":{
                slot1:'',
                slot2:'',
            }
    },
    "Noite":{
            "Incio":{
                slot1:'',
                slot2:'',
            },
            "Fim":{
                slot1:'',
                slot2:'',
            }
    }
}

const ConfigForm = props =>{
    const {config,user,logout} = props
    const [notify,setNotify] = useState({isOpen:false,message:'',type:''})
    const [diasCBList,setDiasCBList] = useState(new Array(dias.length).fill(false))
    const [numSalasAux,setNumSalasAux] = useState(20);
    const [capSalasAux,setCapSalasAux] = useState(200);
    const [periodosCBList,setPeriodosCBList] = useState(new Array(periodos.length).fill(false))
    const [horariosObj,setHorariosObj] = useState(()=>{
        return emptyHorarios
    })

    useEffect(()=>{
        setFormByConfig()
    },[config])

    const setFormByConfig = () =>{
        if (config){
            console.log(config)
            let tempArrayDias = new Array(dias.length).fill(false)
            config.dias.map(dia=>{
                tempArrayDias[dias.indexOf(dia)] = true
            })
            setDiasCBList(tempArrayDias)

            let tempArrayPeriodos = new Array(periodos.length).fill(false)
            config.periodos.map(periodo=>{
                tempArrayPeriodos[periodos.indexOf(periodo)] = true
            })
            setPeriodosCBList(tempArrayPeriodos)
            config.horarios = config?.horarios? config.horarios : {}
            let horariosTemp = {...emptyHorarios}

            if(config.horarios["Manh"]){
                horariosTemp["Manh"]["Incio"].slot1 = config.horarios['Manh']['Incio'].slot1
                horariosTemp["Manh"]["Incio"].slot2 = config.horarios['Manh']['Incio'].slot2
                horariosTemp["Manh"]["Fim"].slot1 = config.horarios['Manh']['Fim'].slot1
                horariosTemp["Manh"]["Fim"].slot2 = config.horarios['Manh']['Fim'].slot2
            }else{
                horariosTemp["Manh"]["Incio"].slot1 = ""
                horariosTemp["Manh"]["Incio"].slot2 = ""
                horariosTemp["Manh"]["Fim"].slot1 = ""
                horariosTemp["Manh"]["Fim"].slot2 = ""
            }

            if(config.horarios["Tarde"]){
                horariosTemp["Tarde"]["Incio"].slot1 = config.horarios['Tarde']['Incio'].slot1
                horariosTemp["Tarde"]["Incio"].slot2 = config.horarios['Tarde']['Incio'].slot2
                horariosTemp["Tarde"]["Fim"].slot1 = config.horarios['Tarde']['Fim'].slot1
                horariosTemp["Tarde"]["Fim"].slot2 = config.horarios['Tarde']['Fim'].slot2
            }else{
                horariosTemp["Tarde"]["Incio"].slot1 = ""
                horariosTemp["Tarde"]["Incio"].slot2 = ""
                horariosTemp["Tarde"]["Fim"].slot1 = ""
                horariosTemp["Tarde"]["Fim"].slot2 = ""
            }

            if(config.horarios["Noite"]){
                horariosTemp["Noite"]["Incio"].slot1 = config.horarios['Noite']['Incio'].slot1
                horariosTemp["Noite"]["Incio"].slot2 = config.horarios['Noite']['Incio'].slot2
                horariosTemp["Noite"]["Fim"].slot1 = config.horarios['Noite']['Fim'].slot1
                horariosTemp["Noite"]["Fim"].slot2 = config.horarios['Noite']['Fim'].slot2
            }else{
                horariosTemp["Noite"]["Incio"].slot1 = ""
                horariosTemp["Noite"]["Incio"].slot2 = ""
                horariosTemp["Noite"]["Fim"].slot1 = ""
                horariosTemp["Noite"]["Fim"].slot2 = ""
            }

            setHorariosObj(horariosTemp)

            setCapSalasAux(config.capSalasAux)
            setNumSalasAux(config.numSalasAux)
        }
    }


    const handleCBDias = position =>{
        const novoDiasCB = diasCBList.map((item, index) =>
            index === position ? !item : item
        );
        setDiasCBList(novoDiasCB)
    }

    const handleCBPeriodos = position =>{
        const novoPeriodosCB = periodosCBList.map((item, index) =>
            index === position ? !item : item
        );
        setPeriodosCBList(novoPeriodosCB)
    }

    const handleNumSalasChange = e =>{
        setNumSalasAux(e.target.value)
    }

    const handleCapSalasChange = e =>{
        setCapSalasAux(e.target.value)
    }

    const handleHorariosChange = (e) =>{
        const {name,value} = e.target
        let periodo = name.slice(0,name.search("-"))
        let horario = name.slice(name.search("-")+1,name.search("_"))
        let slot = name.slice(name.search("_")+1)
        
        if(horario == "Incio" && slot == "slot2"){
            setHorariosObj({
                ...horariosObj,
                [periodo]:{
                    ...horariosObj[periodo],
                    ["Fim"]:{
                        ...horariosObj[periodo]["Fim"],
                        ['slot1']:value
                    },[horario]:{
                        ...horariosObj[periodo][horario],
                        [slot]:value
                    }
                }
            })
        }else{
            setHorariosObj({
                ...horariosObj,
                [periodo]:{
                    ...horariosObj[periodo],
                    [horario]:{
                        ...horariosObj[periodo][horario],
                        [slot]:value
                    }
                }
            })
        }
    }

    const handleBT = e =>{
        let configTemp = {}
        configTemp.dias = []
        dias.map((dia,indexD)=>{
            if(diasCBList[indexD]){
                configTemp.dias.push(dia)
            }
        })
        configTemp.periodos = []
        periodos.map((periodo,indexP)=>{
            if(periodosCBList[indexP]){
                configTemp.periodos.push(periodo)
            }
        })
        configTemp.horarios = {}
        periodos.map((periodo,indexP)=>{
            if(periodosCBList[indexP]){
                configTemp.horarios[periodo] = horariosObj[periodo]
            }
        })
        configTemp.numSalasAux = numSalasAux
        configTemp.capSalasAux = capSalasAux

        let data = {...configTemp}
        ConfigsDataService.updateConfig(data,config._id)
            .then(res=>handleServerResponses('configs',res,setNotify))
            .catch(err=>{
                console.log(err.response.data)
                let notAuthorized = err.response.data?.notAuth ? err.response.data.notAuth : false
                if (notAuthorized){
                    logout()
                }else{
                    handleServerResponses('configs',err,setNotify)
                }
            })
    }

    return(
        <>
            <PageHeader
                title="Configuraes"
                subtitle="Definir dias, periodos e horrios"
                icon={<SettingsIcon/>}
            />
            <Mensagem 
                notify={notify}
                setNotify={setNotify}
            />
            <Paper>
                <Box padding={'25px'}>
                    <Grid container columns={20} spacing={2} alignItems="center">
                        <Grid item xs={20}>
                            <Typography fontSize={'1.1rem'} fontWeight={'405'}> Dias em que a Universidade ministra aulas</Typography>
                        </Grid>
                        <Grid item xs={20}>
                            <FormGroup>
                                {dias.map((dia,index)=>{
                                    return(
                                        <FormControlLabel key ={index} control={
                                            <Checkbox checked={diasCBList[index]} onChange={()=>handleCBDias(index)} name={dia}/>} label={dia}>
                                        </FormControlLabel>
                                    )
                                })}
                            </FormGroup>
                        </Grid>
                        <Grid item xs={20}marginY={1}>
                            <Divider ></Divider>
                        </Grid>
                        <Grid item xs={20}>
                            <Typography fontSize={'1.1rem'} fontWeight={'405'}> Perodos em que a Universidade ministra aulas</Typography>
                        </Grid>
                        <Grid item xs={20}>
                            <FormGroup>
                                {periodos.map((periodo,index)=>{
                                    return(
                                        <FormControlLabel key ={index} control={
                                            <Checkbox checked={periodosCBList[index]} onChange={()=>handleCBPeriodos(index)} name={periodo}/>} label={periodo}>
                                        </FormControlLabel>
                                    )
                                })}
                            </FormGroup>
                        </Grid>
                        <Grid item xs={20}marginY={1}>
                            <Divider ></Divider>
                        </Grid>
                        <Grid item xs={20}>
                            <Typography fontSize={'1.1rem'} fontWeight={'405'}> Horrios em que a Universidade ministra aulas</Typography>
                        </Grid>

                        {periodosCBList[0]?(
                            <>
                                <Grid item xs={20}>
                                    <Typography fontSize={'1rem'} fontWeight={'450'}>Manh</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <Typography marginLeft={5}>Horrios de Incio</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot1"
                                        name = "Manh-Incio_slot1"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Manh']['Incio'].slot1}
                                    />
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot2"
                                        name = "Manh-Incio_slot2"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Manh']['Incio'].slot2}
                                    />
                                </Grid>
                                <Grid item xs={11}></Grid>
                                <Grid item xs={3}>
                                    <Typography marginLeft={5}>Horrios de Trmino</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        disabled
                                        variant="outlined"
                                        label="Slot1"
                                        name = "Manh-Fim_slot1"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Manh']['Fim'].slot1}
                                    />
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot2"
                                        name = "Manh-Fim_slot2"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Manh']['Fim'].slot2}
                                    />
                                </Grid>

                            </>
                        ):(<></>)}
                        {periodosCBList[1]?(
                            <>
                                <Grid item xs={20}>
                                    <Typography fontSize={'1rem'} fontWeight={'450'}>Tarde</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <Typography marginLeft={5}>Horrios de Incio</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot1"
                                        name = "Tarde-Incio_slot1"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Tarde']['Incio'].slot1}
                                    />
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot2"
                                        name = "Tarde-Incio_slot2"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Tarde']['Incio'].slot2}
                                    />
                                </Grid>
                                <Grid item xs={11}></Grid>
                                <Grid item xs={3}>
                                    <Typography marginLeft={5}>Horrios de Trmino</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        disabled
                                        variant="outlined"
                                        label="Slot1"
                                        name = "Tarde-Fim_slot1"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Tarde']['Fim'].slot1}
                                    />
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot2"
                                        name = "Tarde-Fim_slot2"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Tarde']['Fim'].slot2}
                                    />
                                </Grid>

                            </>
                        ):(<></>)}
                        {periodosCBList[2]?(
                            <>
                                <Grid item xs={20}>
                                    <Typography fontSize={'1rem'} fontWeight={'450'}>Noite</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <Typography marginLeft={5}>Horrios de Incio</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot1"
                                        name = "Noite-Incio_slot1"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Noite']['Incio'].slot1}
                                    />
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot2"
                                        name = "Noite-Incio_slot2"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Noite']['Incio'].slot2}
                                    />
                                </Grid>
                                <Grid item xs={11}></Grid>
                                <Grid item xs={3}>
                                    <Typography marginLeft={5}>Horrios de Trmino</Typography>
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        disabled
                                        variant="outlined"
                                        label="Slot1"
                                        name = "Noite-Fim_slot1"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Noite']['Incio'].slot2}
                                    />
                                </Grid>
                                <Grid item xs={3}>
                                    <TextField
                                        variant="outlined"
                                        label="Slot2"
                                        name = "Noite-Fim_slot2"
                                        onChange={handleHorariosChange}
                                        value ={horariosObj['Noite']['Fim'].slot2}
                                    />
                                </Grid>
                            </>
                        ):(<></>)}

                    <Grid item xs={20} marginY="15px">
                            <Divider/>
                    </Grid>
                    <Grid item xs={20}>
                            <Typography fontSize={'1.1rem'} fontWeight={'405'}> Configuraes do prdio auxiliar</Typography>
                    </Grid>
                    <Grid item xs={20}>
                        <TextField
                            variant="outlined"
                            label="Nmero de Salas"
                            name = "numSalasAux"
                            onChange={handleNumSalasChange}
                            value ={numSalasAux}
                        />
                    </Grid>
                    <Grid item xs={20}>
                        <TextField
                            variant="outlined"
                            label="Capacidade das Salas"
                            name = "capSalasAux"
                            onChange={handleCapSalasChange}
                            value ={capSalasAux}
                        />
                    </Grid>
                    </Grid>
                    <br/>
                    <br/>
                    <Button variant="contained" onClick={handleBT}>Salvar</Button>
                    <br/>
                    
                </Box>
            </Paper>
        
        </>

    )


}

export default ConfigForm;