import React from 'react';
import { Box, Typography, Divider, List, ListItem, ListItemText, Alert } from '@mui/material';
import InfoIcon from '@mui/icons-material/Info';
import TuneIcon from '@mui/icons-material/Tune';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

const AjudaSolver = (props) => {
    return (
        <Box sx={{ p: 2 }}>
            <Typography variant="h5" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <InfoIcon color="primary" />
                Como utilizar o Solver
            </Typography>
            
            <Typography variant="body2" color="textSecondary" paragraph>
                Esta ferramenta utiliza um algoritmo de otimizao matemtica para alocar turmas em salas de aula, 
                respeitando as distncias entre departamentos e capacidades fsicas.
            </Typography>

            <Divider sx={{ my: 2 }} />

            <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle1" fontWeight="bold" color="primary" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <TuneIcon fontSize="small" /> Parmetros de Configurao
                </Typography>
                <List dense>
                    <ListItem>
                        <ListItemText 
                            primary={<Typography variant="subtitle2">Folga (Delta)</Typography>}
                            secondary="Define a tolerncia de assentos vazios. Ex: Se Delta  0, o sistema tenta encontrar a sala mais justa possvel. Aumentar este valor permite alocar turmas em salas bem maiores que o necessrio."
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText 
                            primary={<Typography variant="subtitle2">Mnimo de Alunos</Typography>}
                            secondary="Turmas com nmero de alunos matriculados inferior a este valor sero ignoradas pelo otimizador ou tratadas como exceo."
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText 
                            primary={<Typography variant="subtitle2">Prdio Auxiliar</Typography>}
                            secondary="Se marcado, permite que o algoritmo aloque aulas em um prdio comum/compartilhado caso no haja salas disponveis no departamento de origem."
                        />
                    </ListItem>
                </List>
            </Box>

            <Divider sx={{ my: 2 }} />

            <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle1" fontWeight="bold" color="primary" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <AccessTimeIcon fontSize="small" /> Otimizao Avanada (MIP)
                </Typography>
                <Alert severity="info" sx={{ mb: 2 }}>
                    Ajuste estes valores apenas se o resultado estiver demorando muito ou for insatisfatrio.
                </Alert>
                <List dense>
                    <ListItem>
                        <ListItemText 
                            primary={<Typography variant="subtitle2">Tempo Limite (segundos)</Typography>}
                            secondary="Tempo mximo que o algoritmo passar tentando encontrar a melhor soluo matemtica. Se o tempo expirar, ele retornar a melhor soluo encontrada at o momento."
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemText 
                            primary={<Typography variant="subtitle2">MIP Gap (0.0 a 1.0)</Typography>}
                            secondary="Define a margem de erro aceitvel para a soluo tima. Um valor de 0.1 (10%)  mais rpido. Um valor de 0.01 busca uma soluo 99% tima, mas pode levar muito mais tempo de processamento."
                        />
                    </ListItem>
                </List>
            </Box>
        </Box>
    );
};

export default AjudaSolver;