import React from "react";
import {
  Typography,
  Box,
  Divider,
  Chip,
  Alert,
  Stepper,
  Step,
  StepLabel,
  StepContent,
} from "@mui/material";

const AjudaSolicitacoes = () => {
  return (
    <Box sx={{ p: 2, maxWidth: 720 }}>
      <Typography variant="h5" gutterBottom>
        Solicitaes de Acessibilidade
      </Typography>
      <Divider sx={{ mb: 2 }} />

      {/* ====== RESUMO RPIDO ====== */}
      <Alert severity="success" sx={{ mb: 2, fontSize: "0.85rem" }}>
        <b>Resumo rpido:</b> Solicitaes servem para forar o solver a alocar
        determinadas turmas em salas com caractersticas especficas (trreo,
        prancheta, laboratrio, etc.). O sistema troca o departamento da turma
        por um departamento virtual (ex: <b>TERREO-DC</b>) e, na matriz de
        distncias, voc configura distncia <b>0</b> para os prdios adequados
        e <b>999</b> para os demais.
      </Alert>

      {/* ====== O QUE SO ====== */}
      <Typography variant="h6" gutterBottom>
        O que so?
      </Typography>
      <Typography variant="body2" paragraph>
        Quando um aluno cadeirante se matricula em uma turma, ou quando uma
        turma precisa de prancheta de desenho,  necessrio garantir que o
        solver aloque essa turma em uma sala adequada. As solicitaes fazem
        exatamente isso.
      </Typography>
      <Typography variant="body2" paragraph>
        O mecanismo  simples: ao aplicar uma solicitao, o departamento da
        turma  trocado temporariamente por um <b>departamento virtual</b> (ex:
        turma do <b>DC</b> com solicitao de Trreo  departamento vira{" "}
        <b>TERREO-DC</b>). Na pgina de Distncias, esse departamento virtual 
        configurado com distncia <b>0</b> para prdios adequados e <b>999</b>{" "}
        para os demais, fazendo o solver priorizar as salas corretas.
      </Typography>

      <Divider sx={{ my: 2 }} />

      {/* ====== TIPOS DISPONVEIS ====== */}
      <Typography variant="h6" gutterBottom>
        Tipos disponveis
      </Typography>
      <Box sx={{ display: "flex", flexWrap: "wrap", gap: 1, mb: 2 }}>
        <Chip
          label="Trreo"
          size="small"
          sx={{ backgroundColor: "#4caf50", color: "#fff" }}
        />
        <Chip
          label="Prancheta"
          size="small"
          sx={{ backgroundColor: "#ff9800", color: "#fff" }}
        />
        <Chip
          label="Quadro Verde (QV)"
          size="small"
          sx={{ backgroundColor: "#2e7d32", color: "#fff" }}
        />
        <Chip
          label="Quadro Branco (QB)"
          size="small"
          sx={{ backgroundColor: "#1565c0", color: "#fff" }}
        />
        <Chip
          label="Laboratrio"
          size="small"
          sx={{ backgroundColor: "#7b1fa2", color: "#fff" }}
        />
        <Chip
          label="Esp-Norte"
          size="small"
          sx={{ backgroundColor: "#c62828", color: "#fff" }}
        />
        <Chip
          label="Esp-Sul"
          size="small"
          sx={{ backgroundColor: "#00838f", color: "#fff" }}
        />
      </Box>

      <Typography variant="body2" paragraph>
        <b>Trreo:</b> Alunos cadeirantes ou com mobilidade reduzida. Direciona
        para prdios com salas no trreo (ex: AT02(T)).
      </Typography>
      <Typography variant="body2" paragraph>
        <b>Prancheta:</b> Turmas que precisam de salas com pranchetas de desenho
        (ex: AT05.Pr).
      </Typography>
      <Typography variant="body2" paragraph>
        <b>Quadro Verde / Quadro Branco:</b> Turmas que necessitam de um tipo
        especfico de quadro.
      </Typography>
      <Typography variant="body2" paragraph>
        <b>Laboratrio:</b> Turmas que precisam de laboratrios.
      </Typography>
      <Typography variant="body2" paragraph>
        <b>Esp-Norte / Esp-Sul:</b> Direcionamento para espaos especficos no
        campus.
      </Typography>

      <Divider sx={{ my: 2 }} />

      {/* ====== COMO O NOME  GERADO ====== */}
      <Typography variant="h6" gutterBottom>
        Como o departamento virtual  gerado?
      </Typography>
      <Typography variant="body2" paragraph>
        O nome  formado pelo <b>prefixo do tipo</b> +{" "}
        <b>departamento original</b> da turma. Isso garante nomes intuitivos 
        voc sabe imediatamente o que  e de qual departamento veio:
      </Typography>
      <Box
        component="ul"
        sx={{ mb: 2, pl: 2, "& li": { fontSize: "0.85rem", mb: 0.5 } }}
      >
        <li>
          Turma do <b>DC</b> + Trreo  <b>TERREO-DC</b>
        </li>
        <li>
          Turma do <b>DC</b> + Prancheta  <b>PRANCHETA-DC</b>
        </li>
        <li>
          Turma do <b>DFCM</b> + Quadro Verde  <b>QV-DFCM</b>
        </li>
        <li>
          Turma do <b>DFCM</b> + Quadro Branco  <b>QB-DFCM</b>
        </li>
        <li>
          Turma do <b>DC</b> + Laboratrio  <b>LAB-DC</b>
        </li>
        <li>
          Turma do <b>DC</b> + Esp-Norte  <b>NORTE-DC</b>
        </li>
        <li>
          Turma do <b>DC</b> + Esp-Sul  <b>SUL-DC</b>
        </li>
      </Box>

      <Divider sx={{ my: 2 }} />

      {/* ====== PASSO A PASSO ====== */}
      <Typography variant="h6" gutterBottom>
        Passo a passo completo
      </Typography>

      <Alert
        severity="info"
        variant="outlined"
        sx={{ mb: 2, fontSize: "0.8rem" }}
      >
        Os passos abaixo seguem a ordem correta. Siga na sequncia para evitar
        problemas com o solver.
      </Alert>

      <Stepper orientation="vertical" sx={{ mb: 2 }}>
        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              1. Criar prdios particionados (Prdios e Salas)
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Antes de tudo,  preciso que existam prdios separados para as
              salas especiais. Exemplo: se o AT02 tem salas no trreo, crie o
              prdio <b>AT02(T)</b> e cadastre nele <b>apenas</b> as salas do
              trreo. O AT02 original fica com as salas dos andares superiores.
            </Typography>
            <Typography variant="body2" paragraph>
              Para pranchetas, use o sufixo <b>.Pr</b> (ex: <b>AT05.Pr</b>).
              Para laboratrios, use <b>(LAB)</b>. A ideia : o solver s
              consegue diferenciar se as salas estiverem em prdios separados.
            </Typography>
            <Alert
              severity="warning"
              variant="outlined"
              sx={{ fontSize: "0.75rem" }}
            >
              Se esse passo no for feito, o solver no ter como distinguir
              salas no trreo de salas em andares superiores.
            </Alert>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              2. Marcar turmas com solicitao (Turmas  boto direito)
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Na pgina de <b>Turmas</b>, clique com o <b>boto direito</b>
              sobre a turma que precisa de acessibilidade. Um menu aparecer com
              os tipos disponveis. Escolha o tipo adequado.
            </Typography>
            <Typography variant="body2" paragraph>
              A turma ficar marcada com um <b>chip colorido</b> na coluna
              "Solicitao". Voc pode marcar quantas turmas precisar.
            </Typography>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              3. Aplicar solicitaes (esta pgina)
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Nesta pgina, clique em <b>"Aplicar Todas"</b>. O sistema vai
              trocar o departamento de cada turma marcada para o departamento
              virtual correspondente (ex: DC  TERREO-DC).
            </Typography>
            <Alert
              severity="info"
              variant="outlined"
              sx={{ fontSize: "0.75rem" }}
            >
              Esse passo precisa ser feito <b>antes</b> de configurar as
              distncias, pois  aqui que os departamentos virtuais so criados.
            </Alert>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              4. Configurar distncias dos departamentos virtuais (Distncias)
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2" paragraph>
              Agora v  pgina <b>Distncias</b>. Os departamentos virtuais
              (TERREO-DC, PRANCHETA-DC, etc.) j aparecero na lista de
              departamentos. Para cada um, configure:
            </Typography>
            <Box component="ul" sx={{ pl: 2, "& li": { fontSize: "0.8rem" } }}>
              <li>
                Distncia <b>0</b>  prdios que atendem ao requisito (ex:
                AT02(T) para trreo)
              </li>
              <li>
                Distncia <b>999</b>  prdios que NO atendem (penalidade)
              </li>
            </Box>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              5. Rodar o Solver
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2">
              Rode o solver normalmente. Como as turmas com solicitao agora
              tm distncia 0 para prdios adequados e 999 para os demais, o
              solver vai priorizar as salas corretas automaticamente.
            </Typography>
          </StepContent>
        </Step>

        <Step active>
          <StepLabel>
            <Typography variant="body2" sx={{ fontWeight: 600 }}>
              6. (Opcional) Reverter solicitaes
            </Typography>
          </StepLabel>
          <StepContent>
            <Typography variant="body2">
              Aps verificar o resultado, clique em <b>"Reverter Todas"</b>
              para restaurar os departamentos originais das turmas. til se
              quiser rodar o solver novamente sem acessibilidade.
            </Typography>
          </StepContent>
        </Step>
      </Stepper>

      <Divider sx={{ my: 2 }} />

      {/* ====== EXEMPLO PRTICO ====== */}
      <Typography variant="h6" gutterBottom>
        Exemplo prtico completo
      </Typography>
      <Typography variant="body2" paragraph>
        <b>Cenrio:</b> A turma "Introduo  Computao" do departamento DC tem
        um aluno cadeirante. Queremos que o solver aloque essa turma no trreo.
      </Typography>

      <Box
        component="ol"
        sx={{ pl: 2.5, "& li": { fontSize: "0.85rem", mb: 1 } }}
      >
        <li>
          <b>Prdios:</b> J temos AT02(T) com as salas do trreo e AT02 com os
          andares superiores.
        </li>
        <li>
          <b>Turmas:</b> Clico com o boto direito na turma  seleciono
          "Trreo".
        </li>
        <li>
          <b>Solicitaes:</b> Clico "Aplicar Todas"  o departamento da turma
          muda de <b>DC</b> para <b>TERREO-DC</b>.
        </li>
        <li>
          <b>Distncias:</b> Configuro TERREO-DC com dist. <b>0</b> para
          AT02(T), AT03(T) e dist. <b>999</b> para AT02, AT03.
        </li>
        <li>
          <b>Solver:</b> Rodo o solver  a turma  alocada no AT02(T)!
        </li>
        <li>
          <b>Resultado:</b> Confiro que funcionou e posso reverter se quiser.
        </li>
      </Box>

      <Divider sx={{ my: 2 }} />

      {/* ====== DICAS E AVISOS ====== */}
      <Typography variant="h6" gutterBottom>
        Dicas e avisos
      </Typography>

      <Alert severity="warning" sx={{ mb: 1.5, fontSize: "0.8rem" }}>
        <b>Ordem importa!</b> Primeiro aplique as solicitaes (passo 3), depois
        configure as distncias (passo 4), e s ento rode o solver. Se rodar o
        solver sem configurar as distncias, ele apontar distncias faltantes.
      </Alert>

      <Alert severity="info" sx={{ mb: 1.5, fontSize: "0.8rem" }}>
        <b>Departamento original preservado:</b> O sistema sempre guarda o
        departamento original da turma. Voc pode reverter a qualquer momento
        para restaur-lo.
      </Alert>

      <Alert severity="info" sx={{ mb: 1.5, fontSize: "0.8rem" }}>
        <b>Mltiplas turmas do mesmo departamento?</b> Se duas turmas do DC tm
        solicitao de Trreo, ambas usaro o mesmo departamento virtual
        (TERREO-DC). Basta configurar as distncias de TERREO-DC uma vez.
      </Alert>

      <Alert severity="info" sx={{ fontSize: "0.8rem" }}>
        <b>Turmas de departamentos diferentes?</b> Cada departamento gera seu
        prprio departamento virtual (TERREO-DC, TERREO-DFCM, etc.). Configure
        as distncias de cada um separadamente na pgina de Distncias.
      </Alert>
    </Box>
  );
};

export default AjudaSolicitacoes;
